import React, { useEffect, useState } from "react";
import NavBarMember from "../NavBarMember";
import API from "../../utils/API";
// import PostCard from "../PostCard";

function Profile() {
    // Setting initial state of confession posts
    const [posts, setConfessions] = useState([]);

    // Load all confessions and store them with setConfessions
    useEffect(() => {
        loadConfessions()
    }, []);

    // Loads all confession posts and set them to confessions
    function loadConfessions() {
        API.getConfessions()
            .then(res =>
                setConfessions(res.data)
            )
            .catch(err => console.log(err));
    };

    // useEffect(function() {
    //     async function getConfessions() {
    //         try {
    //             const response = await axios.get("/api/member/profile");
    //             setConfessions(response.data);
    //         } catch(err) {
    //             console.log(err);
    //         }
    //     }
    //     getConfessions();
    // }, []);

    return (
        <div>
            <NavBarMember />
            <section>
                <p>Name: </p>
                <p>Info: </p>
            </section>
            <section>
                {posts.length ? (
                    <div>
                        {posts.map(post => (
                            <div key={post._id}>
                                <p>{post.content}</p>
                            </div>
                        ))}
                    </div>
                ) : (
                    <h3>You have no post yet!</h3>
                )}
            </section>
        </div>
    );

};

export default Profile;
